import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Button from '@material-ui/core/Button';
import userStore from '../stores/UserStore';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Container from '@material-ui/core/Container';
import TextField from '@material-ui/core/TextField';
import SearchIcon from '@material-ui/icons/Search';
import axios from 'axios';
import EditIcon from '@material-ui/icons/Edit';
import DoneIcon from '@material-ui/icons/Done';
import NativeSelect from '@material-ui/core/NativeSelect';
import Avatar from '@material-ui/core/Avatar';
import Alert from '@material-ui/lab/Alert';
import Snackbar from '@material-ui/core/Snackbar';
import CancelIcon from '@material-ui/icons/Cancel';
import DeleteIcon from '@material-ui/icons/Delete';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import TimerOffIcon from '@material-ui/icons/TimerOff';
import Divider from '@material-ui/core/Divider';
import AccessTimeIcon from '@material-ui/icons/AccessTime';
import {Link} from 'react-router-dom';
import CssBaseline from '@material-ui/core/CssBaseline';
import Tooltip from '@material-ui/core/Tooltip';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Grid from '@material-ui/core/Grid';
import TimelapseIcon from '@material-ui/icons/Timelapse';


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1
    },
    menuButton: {
        marginRight: theme.spacing(2)
    },
    title: {
        flexGrow: 1
    },
    userAvatar: {
        marginRight: theme.spacing(2)
    },
    table: {
        '& .MuiTableCell-root': {
            whiteSpace: 'nowrap'
        },
        '& .MuiTableHead-root': {
            '& .MuiTableRow-root': {
                '& .MuiTableCell-root': {
                    backgroundColor: theme.palette.success.main
                }
            }
        }
    },
    container: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(5),
    },
    gridContainer: {
        marginBottom: theme.spacing(3)
    },
    tableToolbar: {
        '& .MuiTextField-root': {
            marginRight: theme.spacing(2),
            marginLeft: theme.spacing(1),
            width: 100
        },
        '& .MuiIconButton-root': {
            marginLeft: theme.spacing(2)
        },
        marginBottom: theme.spacing(1)
    },
    disableCell: {
    },
    holidayCell: {
        color: '#f44336',
    },
    customerField: {
        width: 50
    },
    dateField: {
        width:50
    },
    drawerList: {
        width: 250
    }
}));

const dayOfWeek = ['日', '月', '火', '水', '木', '金', '土'];
const kindMap = {
    '0': '',
    '10': '有休', '11': '有休(AM)', '12': '有休(PM)',
    '20': '欠勤', '21': '欠勤(AM)', '22': '欠勤(PM)',
    '30': '特休', '31': '特休(AM)', '32': '特休(PM)',
    '40': '代休', '41': '代休(AM)', '42': '代休(PM)',
    '50': '休出'};

export default function TimeRecord() {
    const classes = useStyles();
    const [user] = useState(userStore.getUser());
    const [year, setYear] = useState(new Date().getFullYear());
    const [month, setMonth] = useState(new Date().getMonth() + 1);
    const [rows, setRows] = useState([]);
    const [severity, setSeverity] = useState('error');
    const [message, setMessage] = useState('');
    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [oldRows, setOldRows] = useState({});
    const [drawerOpen, setDrawerOpen] = useState(false);
    const [monthTotalTime, setMonthTotalTime] = useState('0:00');

    /**
     * 勤怠記録情報を生成します。
     *
     * @param edit 編集状態
     * @param id 勤怠記録情報ID
     * @param year 年
     * @param month 月
     * @param date 日
     * @param day 曜日
     * @param holiday 休日
     * @param customer 客先
     * @param kind 勤休
     * @param start_time 始業
     * @param end_time 終業
     * @param total_time 合計
     * @param note 備考
     * @returns {{date: *, note: *, edit: *, year: *, kind: *, end_time: *, holiday: *, start_time: *, month: *, id: *, total_time: *, day: *, customer: *}}
     */
    const createData = (edit, id, year, month, date, day, holiday, customer, kind, start_time, end_time, total_time, note) => {
        return { edit, id, year, month, date, day, holiday, customer, kind, start_time, end_time, total_time, note };
    };

    useEffect(() => {
        const now = new Date();
        const y = now.getFullYear();
        const m = now.getMonth() + 1;
        axios.get(`${process.env.REACT_APP_REST_URI}/time_records/${y}/${m}`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then((response) => {
            axios.get(`https://holidays-jp.github.io/api/v1/${y}/date.json`).then((response2) => {
                const records = response.data['records'];
                const japaneseHolidays = response2.data;
                const inputDate = new Date(y, m - 1, 1);
                const rows = [];
                while (true) {
                    const date = inputDate.getDate();
                    const day = inputDate.getDay();
                    // 祝日サービスのキーは'YYYY-MM-dd'形式なので桁数を合わせた祝日キーを生成
                    const holidayKey = `${y}-${('0' + m).slice(-2)}-${('0' + date).slice(-2)}`;
                    // 日曜, 土曜, 祝日の場合は'H'を設定
                    const holiday = (day === 0 || day === 6 || holidayKey in japaneseHolidays) ? 'H' : '';
                    const targetTime = records.find(t => t['date'] === date);
                    if (targetTime) {
                        rows.push(createData(
                            false,
                            targetTime['time_record_id'],
                            y,
                            m,
                            date,
                            dayOfWeek[day],
                            holiday,
                            targetTime['customer'],
                            targetTime['kind'],
                            targetTime['start_time'],
                            targetTime['end_time'],
                            targetTime['total_time'],
                            targetTime['note']));
                    }
                    else {
                        rows.push(createData(
                            false,
                            0,
                            y,
                            m,
                            date,
                            dayOfWeek[day],
                            holiday,
                            '',
                            0,
                            '',
                            '',
                            '',
                            japaneseHolidays[holidayKey]));
                    }
                    inputDate.setDate(date + 1);
                    if (Number(m) !== (inputDate.getMonth() + 1)) {
                        break;
                    }
                }
                setMonthTotalTime(sumTotalTime(rows));
                setRows(rows);
                setOldRows({});
            }).catch((error) => {
                setSeverity('error');
                setMessage(error['message']);
                setSnackbarOpen(true);
            });
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    }, []);

    /**
     * 検索ボタンがクリックされた場合に呼ばれます。
     */
    const handleClickSearch = () => {
        // 年の入力がない場合はエラー
        if (!year) {
            setSeverity('error');
            setMessage('年が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        // 年が2020年未満、または現在年を超える場合はエラー
        if (Number(year) < 2020 || Number(year) > new Date().getFullYear()) {
            setSeverity('error');
            setMessage('年は「2020≦入力年≦現在年」の範囲で入力してください。');
            setSnackbarOpen(true);
            return;
        }

        axios.get(`${process.env.REACT_APP_REST_URI}/time_records/${year}/${month}`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then((response) => {
            axios.get(`https://holidays-jp.github.io/api/v1/${year}/date.json`).then((response2) => {
                const records = response.data['records'];
                const japaneseHolidays = response2.data;
                const inputDate = new Date(year, month - 1, 1);
                const rows = [];
                while (true) {
                    const date = inputDate.getDate();
                    const day = inputDate.getDay();
                    // 祝日サービスのキーは'YYYY-MM-dd'形式なので桁数を合わせた祝日キーを生成
                    const holidayKey = `${year}-${('0' + month).slice(-2)}-${('0' + date).slice(-2)}`;
                    // 日曜, 土曜, 祝日の場合は'H'を設定
                    const holiday = (day === 0 || day === 6 || holidayKey in japaneseHolidays) ? 'H' : '';
                    const targetTime = records.find(t => t['date'] === date);
                    if (targetTime) {
                        rows.push(createData(
                            false,
                            targetTime['time_record_id'],
                            year,
                            month,
                            date,
                            dayOfWeek[day],
                            holiday,
                            targetTime['customer'],
                            targetTime['kind'],
                            targetTime['start_time'],
                            targetTime['end_time'],
                            targetTime['total_time'],
                            targetTime['note']));
                    }
                    else {
                        rows.push(createData(
                            false,
                            0,
                            year,
                            month,
                            date,
                            dayOfWeek[day],
                            holiday,
                            '',
                            0,
                            '',
                            '',
                            '',
                            japaneseHolidays[holidayKey]));
                    }
                    inputDate.setDate(date + 1);
                    if (Number(month) !== (inputDate.getMonth() + 1)) {
                        break;
                    }
                }
                setMonthTotalTime(sumTotalTime(rows));
                setRows(rows);
                setOldRows({});
            }).catch((error) => {
                setSeverity('error');
                setMessage(error['message']);
                setSnackbarOpen(true);
            });
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    };

    /**
     * 一日合計時間を合計した結果を文字列として取得します。
     *
     * @param rows 勤怠記録情報リスト
     * @returns {string} HH:mm
     */
    const sumTotalTime = (rows) => {
        let hour = 0;
        let minute = 0;
        rows.forEach((row) => {
            if (row.total_time) {
                hour += Number(row.total_time.split(':')[0]);
                minute += Number(row.total_time.split(':')[1])
            }
        });
        hour += Math.floor(minute / 60); // 少数以下切り捨て
        minute = (minute % 60);
        return hour + ':' + ('00' + minute).slice(-2);
    };

    /**
     * 確定ボタンがクリックされた場合に呼ばれます。
     *
     * @param row 勤怠情報
     */
    const handleClickDone = (row) => {
        const regex = RegExp(/\d{1,2}:\d{1,2}/);
        if (row.start_time && !regex.test(row.start_time)) {
            setSeverity('error');
            setMessage('始業は「HH:mm」形式で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (row.end_time && !regex.test(row.end_time)) {
            setSeverity('error');
            setMessage('終業は「HH:mm」形式で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (row.start_time && row.end_time && !validationTime(row.start_time, row.end_time)) {
            setSeverity('error');
            setMessage('始業は終業よりも前の時間を入力してください。');
            setSnackbarOpen(true);
            return;
        }

        const rowYear = row.year;
        const rowMonth = row.month;
        const rowDate = row.date;
        if (0 === row.id) {
            axios.post(`${process.env.REACT_APP_REST_URI}/time_records/${rowYear}/${rowMonth}/${rowDate}`, {
                customer: row.customer,
                kind: row.kind,
                start_time: row.start_time,
                end_time: row.end_time,
                note: row.note
            }, {
                params: {
                    token: userStore.getAccessToken()
                }
            }).then((response) => {
                recordToRow(response.data['record'], row);
                setMonthTotalTime(sumTotalTime(rows));
                setRows(rows.slice());
                setSeverity('success');
                setMessage('更新しました。');
                setSnackbarOpen(true);
            }).catch((error) => {
                setSeverity('error');
                setMessage(error['message']);
                setSnackbarOpen(true);
            });

        }
        else {
            axios.put(`${process.env.REACT_APP_REST_URI}/time_records/${rowYear}/${rowMonth}/${rowDate}`, {
                customer: row.customer,
                kind: row.kind,
                start_time: row.start_time,
                end_time: row.end_time,
                note: row.note
            }, {
                params: {
                    token: userStore.getAccessToken()
                }
            }).then((response) => {
                recordToRow(response.data['record'], row);
                setMonthTotalTime(sumTotalTime(rows));
                setRows(rows.slice());
                setSeverity('success');
                setMessage('更新しました。');
                setSnackbarOpen(true);
            }).catch((error) => {
                setSeverity('error');
                setMessage(error['message']);
                setSnackbarOpen(true);
            });
        }
    };

    /**
     * サービスから取得した勤怠記録情報を行データに設定します。
     *
     * @param record 勤怠記録情報
     * @param row 行データ
     */
    const recordToRow = (record, row) => {
        row.edit = false;
        row.id = record['time_record_id'];
        row.customer = record['customer'];
        row.kind = record['kind'];
        row.start_time = record['start_time'];
        row.end_time = record['end_time'];
        row.total_time = record['total_time'];
        row.note = record['note'];
    };

    /**
     * 編集ボタンがクリックされた場合に呼ばれます。
     *
     * @param row 勤怠情報
     */
    const handleClickEdit = (row) => {
        delete oldRows[String(row.date)];
        setOldRows(oldRows);
        row.edit = true;
        setRows(rows.slice());
    };

    /**
     * キャンセルボタンがクリックされた場合に呼ばれます。
     *
     * @param row 勤怠情報
     */
    const handleClickCancel = (row) => {
        const oldRow = oldRows[String(row.date)];
        if (oldRow) {
            row.customer = oldRow.customer;
            row.kind = oldRow.kind;
            row.start_time = oldRow.start_time;
            row.end_time = oldRow.end_time;
            row.total_time = oldRow.total_time;
            row.note = oldRow.note;
            delete oldRows[String(row.date)];
            setOldRows(oldRows);
        }
        row.edit = false;
        setRows(rows.slice());
    };

    /**
     * 削除ボタンがクリックされた場合に呼ばれます。
     *
     * @param row 勤怠情報
     */
    const handleClickDelete = (row) => {
        const rowYear = row.year;
        const rowMonth = row.month;
        const rowDate = row.date;
        axios.delete(`${process.env.REACT_APP_REST_URI}/time_records/${rowYear}/${rowMonth}/${rowDate}`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then(() => {
            row.id = 0;
            row.customer = null;
            row.kind = 0;
            row.start_time = null;
            row.end_time = null;
            row.total_time = null;
            row.note = null;
            setMonthTotalTime(sumTotalTime(rows));
            setRows(rows.slice());
            setSeverity('success');
            setMessage('削除しました。');
            setSnackbarOpen(true);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    };

    /**
     * 勤怠情報をバックアップします。
     *
     * @param row 勤怠情報
     */
    const backupRow = (row) => {
        if (!oldRows.hasOwnProperty(String(row.date))) {
            oldRows[String(row.date)] = Object.assign({}, row);
        }
    };

    /**
     * 客先が変更された場合に呼ばれます。
     *
     * @param row 勤怠情報
     * @param customer 客先
     */
    const handleChangeCustomer = (row, customer) => {
        backupRow(row);
        row.customer = customer;
        setRows(rows.slice());
    };

    /**
     * 勤休が変更された場合に呼ばれます。
     *
     * @param row 勤怠情報
     * @param kind 勤休
     */
    const handleChangeKind = (row, kind) => {
        backupRow(row);
        row.kind = kind;
        setRows(rows.slice());
    };

    /**
     * 始業が変更された場合に呼ばれます。
     *
     * @param row 勤怠情報
     * @param start_time 始業
     */
    const handleChangeStartTime = (row, start_time) => {
        backupRow(row);
        row.start_time = start_time;
        setRows(rows.slice());
    };

    /**
     * 終業が変更された場合に呼ばれます。
     *
     * @param row 勤怠情報
     * @param end_time 終業
     */
    const handleChangeEndTime = (row, end_time) => {
        backupRow(row);
        row.end_time = end_time;
        setRows(rows.slice());
    };

    /**
     * 備考が変更された場合に呼ばれます。
     *
     * @param row 勤怠情報
     * @param note 備考
     */
    const handleChangeNote = (row, note) => {
        backupRow(row);
        row.note = note;
        setRows(rows.slice());
    };

    /**
     * 勤休の選択肢を描画するコンポーネントです。
     *
     * @returns {[]} 選択肢リスト
     * @constructor
     */
    const KindOptions = () => {
        const options = [];
        for (let [key, value] of Object.entries(kindMap)) {
            options.push(<option key={key} value={key}>{value}</option>);
        }
        return options;
    };

    /**
     * 月の選択肢を描画するコンポーネントです。
     *
     * @returns {[]} 選択肢リスト
     * @constructor
     */
    const MonthOptions = () => {
        const options = [];
        for (let month = 1; month <= 12; month++) {
            options.push(<option key={month} value={month}>{month}</option>);
        }
        return options;
    };

    /**
     * スナックバーの閉じるイベント時に呼ばれます。
     *
     * @param event イベント
     * @param reason 理由
     */
    const handleCloseSnackbar = (event, reason) => {
        if ('clickaway' === reason) {
            return;
        }
        setSnackbarOpen(false);
    };

    /**
     * 時間の前後検証を実施します。
     *
     * @param start 始業
     * @param end 終業
     * @returns {boolean} true: 検証OK, false: 検証NG
     */
    const validationTime = (start, end) => {
        const now = new Date();
        const starts = start.split(':');
        const ends = end.split(':');
        const sdt = new Date(now.getFullYear(), now.getMonth(), now.getDate(), Number(starts[0]), Number(starts[1]));
        const edt = new Date(now.getFullYear(), now.getMonth(), now.getDate(), Number(ends[0]), Number(ends[1]));
        return sdt < edt;
    };

    return (
        <form className={classes.root}>
            <CssBaseline />
            
            <AppBar position="sticky">
                <Toolbar>
                    <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu" onClick={() => setDrawerOpen(true)}>
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" className={classes.title}>
                        株式会社ベイクラフト
                    </Typography>
                    <Button color="inherit">
                        <Avatar alt="avatar" src={user['profile']['image_48']} className={classes.userAvatar} />
                        {user['profile']['real_name']}
                    </Button>
                </Toolbar>
            </AppBar>

            <Drawer open={drawerOpen} onClose={() => setDrawerOpen(false)}>
                <div className={classes.drawerList} onClick={() => setDrawerOpen(false)} onKeyDown={() => setDrawerOpen(false)}>
                    <List component="nav">
                        <ListItem button component={Link} to="/timerecord">
                            <ListItemIcon><AccessTimeIcon/></ListItemIcon>
                            <ListItemText primary="勤怠記録"/>
                        </ListItem>
                        <Divider />
                        <ListItem button component={Link} to="/fixedtime">
                            <ListItemIcon><TimelapseIcon/></ListItemIcon>
                            <ListItemText primary="所定時間設定"/>
                        </ListItem>
                        <ListItem button component={Link} to="/breaktime">
                            <ListItemIcon><TimerOffIcon/></ListItemIcon>
                            <ListItemText primary="休憩時間設定"/>
                        </ListItem>
                    </List>
                </div>
            </Drawer>

            <Snackbar open={snackbarOpen} autoHideDuration={6000} onClose={handleCloseSnackbar}>
                <Alert severity={severity} variant="filled" onClose={handleCloseSnackbar}>{message}</Alert>
            </Snackbar>

            <Container maxWidth="lg" className={classes.container}>
                <Toolbar className={classes.tableToolbar}>
                    <Typography variant="h6" className={classes.title}>
                        勤怠記録
                    </Typography>
                    <TextField required={true} label="年" margin="normal" defaultValue={year} onChange={(event) => setYear(event.target.value)} />
                    <FormControl required={true} margin="normal">
                        <InputLabel htmlFor="select-month">月</InputLabel>
                        <NativeSelect value={month} inputProps={{id: 'select-month'}}
                                      onChange={(event) => setMonth(event.target.value)}>
                            <MonthOptions />
                        </NativeSelect>
                    </FormControl>
                    <Tooltip title="検索">
                        <IconButton onClick={handleClickSearch}>
                            <SearchIcon />
                        </IconButton>
                    </Tooltip>
                </Toolbar>
                <Grid container={true} spacing={1} className={classes.gridContainer}>
                    <Grid item={true} xs={7}>
                        <TableContainer>
                            <Table size="small" className={classes.table}>
                                <TableHead>
                                    <TableRow>
                                        <TableCell align="center">所定時間</TableCell>
                                        <TableCell align="center">実働時間</TableCell>
                                        <TableCell align="center">普通残業</TableCell>
                                        <TableCell align="center">深夜残業</TableCell>
                                        <TableCell align="center">法休普残</TableCell>
                                        <TableCell align="center">法休深残</TableCell>
                                        <TableCell align="center">控除</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    <TableRow>
                                        <TableCell align="center"></TableCell>
                                        <TableCell align="center">{monthTotalTime}</TableCell>
                                        <TableCell align="center"></TableCell>
                                        <TableCell align="center"></TableCell>
                                        <TableCell align="center"></TableCell>
                                        <TableCell align="center"></TableCell>
                                        <TableCell align="center"></TableCell>
                                    </TableRow>
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Grid>
                </Grid>
                <TableContainer>
                    <Table className={classes.table} aria-label="timerecords" size="small">
                        <TableHead>
                            <TableRow>
                                <TableCell align="center" scope="row">日付</TableCell>
                                <TableCell align="center">曜日</TableCell>
                                <TableCell align="center">休日</TableCell>
                                <TableCell align="center">客先</TableCell>
                                <TableCell align="center">勤休</TableCell>
                                <TableCell align="center">始業</TableCell>
                                <TableCell align="center">終業</TableCell>
                                <TableCell align="center">合計</TableCell>
                                <TableCell align="center">備考</TableCell>
                                <TableCell align="center">操作</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows.map(row => (
                                <TableRow key={row.date}>
                                    <TableCell align="center" className={classes.disableCell}>{row.date}</TableCell>
                                    <TableCell align="center" className={classes.disableCell}>{row.day}</TableCell>
                                    <TableCell align="center" className={classes.holidayCell}>{row.holiday}</TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.customer} className={classes.customerField}
                                                       inputProps={{style: {textAlign: 'center'}}}
                                                       autoFocus={true}
                                                       onChange={(event) => handleChangeCustomer(row, event.target.value)} />
                                        ) : (
                                            <span>{row.customer}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <NativeSelect value={row.kind}
                                                          onChange={(event) => handleChangeKind(row, event.target.value)}>
                                                <KindOptions />
                                            </NativeSelect>
                                        ) : (
                                            <span>{kindMap[row.kind]}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.start_time} className={classes.dateField}
                                                       inputProps={{style: {textAlign: 'center'}}}
                                                       onChange={(event) => handleChangeStartTime(row, event.target.value)} />
                                        ) : (
                                            <span>{row.start_time}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.end_time} className={classes.dateField}
                                                       inputProps={{style: {textAlign: 'center'}}}
                                                       onChange={(event) => handleChangeEndTime(row, event.target.value)} />
                                        ) : (
                                            <span>{row.end_time}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center" className={classes.disableCell}>{row.total_time}</TableCell>
                                    {row.edit ? (
                                        <TableCell align="center">
                                            <TextField defaultValue={row.note}
                                                       onChange={(event) => handleChangeNote(row, event.target.value)} />
                                        </TableCell>
                                    ) : (
                                        <TableCell><span>{row.note}</span></TableCell>
                                    )}
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <div>
                                                <IconButton aria-label="done" onClick={() => handleClickDone(row)}>
                                                    <DoneIcon />
                                                </IconButton>
                                                <IconButton aria-label="cancel" onClick={() => handleClickCancel(row)}>
                                                    <CancelIcon />
                                                </IconButton>
                                            </div>
                                        ) : (
                                            <div>
                                                <IconButton aria-label="edit" onClick={() => handleClickEdit(row)}>
                                                    <EditIcon />
                                                </IconButton>
                                                <IconButton aria-label="delete" onClick={() => handleClickDelete(row)}>
                                                    <DeleteIcon />
                                                </IconButton>
                                            </div>
                                        )}
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Container>
        </form>
    );
}
