import React, {useState, useEffect} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import {Link} from 'react-router-dom';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import AccessTimeIcon from '@material-ui/icons/AccessTime';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import TimerOffIcon from '@material-ui/icons/TimerOff';
import userStore from '../stores/UserStore';
import Container from '@material-ui/core/Container';
import AddIcon from '@material-ui/icons/Add';
import TableContainer from '@material-ui/core/TableContainer';
import Table from '@material-ui/core/Table';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';
import CssBaseline from '@material-ui/core/CssBaseline';
import TableBody from '@material-ui/core/TableBody';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import DoneIcon from '@material-ui/icons/Done';
import CancelIcon from '@material-ui/icons/Cancel';
import TextField from '@material-ui/core/TextField';
import Tooltip from '@material-ui/core/Tooltip';
import axios from 'axios';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import NativeSelect from '@material-ui/core/NativeSelect';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Snackbar from '@material-ui/core/Snackbar';
import Alert from '@material-ui/lab/Alert';
import CachedIcon from '@material-ui/icons/Cached';
import TimelapseIcon from '@material-ui/icons/Timelapse';


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1
    },
    menuButton: {
        marginRight: theme.spacing(2)
    },
    title: {
        flexGrow: 1
    },
    userAvatar: {
        marginRight: theme.spacing(2)
    },
    drawerList: {
        width: 250
    },
    container: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
    },
    tableToolbar: {
        marginBottom: theme.spacing(1)
    },
    table: {
        '& .MuiTableCell-root': {
            whiteSpace: 'nowrap',
            '& .MuiTextField-root': {
                width: 100
            }
        },
        '& .MuiTableHead-root': {
            '& .MuiTableRow-root': {
                '& .MuiTableCell-root': {
                    backgroundColor: theme.palette.success.main
                }
            }
        }
    }
}));

export default function BreakTime() {
    const classes = useStyles();
    const [user] = useState(userStore.getUser());
    const [drawerOpen, setDrawerOpen] = useState(false);
    const [rows, setRows] = useState([]);
    const [oldRows, setOldRows] = useState({});
    const [addModalOpen, setAddModelOpen] = useState(false);
    const [year, setYear] = useState('2020');
    const [month, setMonth] = useState('1');
    const [customer, setCustomer] = useState('');
    const [startTime, setStartTime] = useState('');
    const [endTime, setEndTime] = useState('');
    const [severity, setSeverity] = useState('error');
    const [message, setMessage] = useState('');
    const [snackbarOpen, setSnackbarOpen] = useState(false);

    /**
     * 休憩時間情報を生成します。
     *
     * @param edit 編集状態
     * @param id 休憩時間情報ID
     * @param year 年
     * @param month 月
     * @param customer 客先
     * @param start_time 開始時間
     * @param end_time 終了時間
     * @returns {{start_time: *, month: *, edit: *, year: *, end_time: *, id: *, customer: *}}
     */
    const createData = (edit, id, year, month, customer, start_time, end_time) => {
        return {edit, id, year, month, customer, start_time, end_time};
    };

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_REST_URI}/break_times`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then((response) => {
            const records = response.data['records'];
            const rows = records.map(record => {
                return createData(false,
                    record['break_time_id'],
                    record['year'],
                    record['month'],
                    record['customer'],
                    record['start_time'],
                    record['end_time']);
            });
            setRows(rows);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    }, []);

    /**
     * 追加ボタンがクリックされた場合に呼ばれます。
     */
    const handleClickAdd = () => {
        const now = new Date();
        setYear(String(now.getFullYear()));
        setMonth(String(now.getMonth() + 1));
        setCustomer('');
        setStartTime('');
        setEndTime('');
        setAddModelOpen(true);
    };

    /**
     * リロードボタンがクリックされた場合に呼ばれます。
     */
    const handleClickReload = () => {
        axios.get(`${process.env.REACT_APP_REST_URI}/break_times`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then((response) => {
            const records = response.data['records'];
            const rows = records.map(record => {
                return createData(false,
                    record['break_time_id'],
                    record['year'],
                    record['month'],
                    record['customer'],
                    record['start_time'],
                    record['end_time']);
            });
            setRows(rows);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    };

    /**
     * 休憩時間追加ダイアログでキャンセルボタンがクリックされた場合に呼ばれます。
     */
    const handleClickDialogCancel = () => {
        setAddModelOpen(false);
    };

    /**
     * 休憩時間追加ダイアログで追加ボタンがクリックされた場合に呼ばれます。
     */
    const handleClickDialogAdd = () => {
        if (!year) {
            setSeverity('error');
            setMessage('年が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        if (!customer) {
            setSeverity('error');
            setMessage('客先が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        // 年が2020年未満、または現在年を超える場合はエラー
        if (Number(year) < 2020 || Number(year) > new Date().getFullYear()) {
            setSeverity('error');
            setMessage('年は「2020≦入力年≦現在年」の範囲で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (!startTime) {
            setSeverity('error');
            setMessage('開始時間が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        if (!endTime) {
            setSeverity('error');
            setMessage('終了時間が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        const regex = RegExp(/\d{1,2}:\d{1,2}/);
        if (!regex.test(startTime)) {
            setSeverity('error');
            setMessage('開始時間は「HH:mm」形式で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (!regex.test(endTime)) {
            setSeverity('error');
            setMessage('終了時間は「HH:mm」形式で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (!validationTime(startTime, endTime)) {
            setSeverity('error');
            setMessage('開始時間は終了時間よりも前の時間を入力してください。');
            setSnackbarOpen(true);
            return;
        }

        axios.post(`${process.env.REACT_APP_REST_URI}/break_times`, {
            year: year,
            month: month,
            customer: customer,
            start_time: startTime,
            end_time: endTime
        }, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then((response) => {
            const record = response.data['record'];
            const row = createData(
                false,
                record['break_time_id'],
                record['year'],
                record['month'],
                record['customer'],
                record['start_time'],
                record['end_time']);
            rows.push(row);
            setRows(rows.slice());
            setAddModelOpen(false);
            setSeverity('success');
            setMessage('休憩時間を追加しました。');
            setSnackbarOpen(true);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    };

    /**
     * 確定ボタンがクリックされた場合に呼ばれます。
     *
     * @param row 休憩時間情報
     */
    const handleClickDone = (row) => {
        if (!row.year) {
            setSeverity('error');
            setMessage('年が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        if (!row.customer) {
            setSeverity('error');
            setMessage('客先が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        // 年が2020年未満、または現在年を超える場合はエラー
        if (Number(row.year) < 2020 || Number(row.year) > new Date().getFullYear()) {
            setSeverity('error');
            setMessage('年は「2020≦入力年≦現在年」の範囲で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (!row.start_time) {
            setSeverity('error');
            setMessage('開始時間が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        if (!row.end_time) {
            setSeverity('error');
            setMessage('終了時間が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        const regex = RegExp(/\d{1,2}:\d{1,2}/);
        if (!regex.test(row.start_time)) {
            setSeverity('error');
            setMessage('開始時間は「HH:mm」形式で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (!regex.test(row.end_time)) {
            setSeverity('error');
            setMessage('終了時間は「HH:mm」形式で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (!validationTime(row.start_time, row.end_time)) {
            setSeverity('error');
            setMessage('開始時間は終了時間よりも前の時間を入力してください。');
            setSnackbarOpen(true);
            return;
        }

        axios.put(`${process.env.REACT_APP_REST_URI}/break_times/${row.id}`, {
            year: row.year,
            month: row.month,
            customer: row.customer,
            start_time: row.start_time,
            end_time: row.end_time
        }, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then((response) => {
            const record = response.data['record'];
            row.edit = false;
            row.year = record['year'];
            row.month = record['month'];
            row.customer = record['customer'];
            row.start_time = record['start_time'];
            row.end_time = record['end_time'];
            setRows(rows.slice());
            setSeverity('success');
            setMessage('更新しました。');
            setSnackbarOpen(true);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    };

    /**
     * キャンセルボタンがクリックされた場合に呼ばれます。
     *
     * @param row 休憩時間情報
     */
    const handleClickCancel = (row) => {
        const oldRow = oldRows[String(row.id)];
        if (oldRow) {
            row.customer = oldRow.customer;
            row.start_time = oldRow.start_time;
            row.end_time = oldRow.end_time;
            delete oldRows[String(row.id)];
            setOldRows(oldRows);
        }
        row.edit = false;
        setRows(rows.slice());
    };

    /**
     * 編集ボタンがクリックされた場合に呼ばれます。
     *
     * @param row 休憩時間情報
     */
    const handleClickEdit = (row) => {
        delete oldRows[String(row.id)];
        setOldRows(oldRows);
        row.edit = true;
        setRows(rows.slice());
    };

    /**
     * 削除ボタンがクリックされた場合に呼ばれます。
     *
     * @param row 休憩時間情報
     */
    const handleClickDelete = (row) => {
        axios.delete(`${process.env.REACT_APP_REST_URI}/break_times/${row.id}`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then(() => {
            delete rows[rows.indexOf(row)];
            setRows(rows.slice());
            setSeverity('success');
            setMessage('削除しました。');
            setSnackbarOpen(true);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    };

    /**
     * 年が変更された場合に呼ばれます。
     *
     * @param row 休憩時間情報
     * @param year 年
     */
    const handleChangeYear = (row, year) => {
        backupRow(row);
        row.year = year;
        setRows(rows.slice());
    };

    /**
     * 月が変更された場合に呼ばれます。
     *
     * @param row 休憩時間情報
     * @param month 月
     */
    const handleChangeMonth = (row, month) => {
        backupRow(row);
        row.month = month;
        setRows(rows.slice());
    };

    /**
     * 客先が変更された場合に呼ばれます。
     *
     * @param row 休憩時間情報
     * @param customer 客先
     */
    const handleChangeCustomer = (row, customer) => {
        backupRow(row);
        row.customer = customer;
        setRows(rows.slice());
    };

    /**
     * 開始時間が変更された場合に呼ばれます。
     *
     * @param row 休憩時間情報
     * @param start_time 開始時間
     */
    const handleChangeStartTime = (row, start_time) => {
        backupRow(row);
        row.start_time = start_time;
        setRows(rows.slice());
    };

    /**
     * 終了時間が変更された場合に呼ばれます。
     *
     * @param row 休憩時間情報
     * @param end_time 終了時間
     */
    const handleChangeEndTime = (row, end_time) => {
        backupRow(row);
        row.end_time = end_time;
        setRows(rows.slice());
    };

    /**
     * 休憩時間情報をバックアップします。
     *
     * @param row 休憩時間情報
     */
    const backupRow = (row) => {
        if (!oldRows.hasOwnProperty(String(row.id))) {
            oldRows[String(row.id)] = Object.assign({}, row);
        }
    };

    /**
     * 月の選択肢を描画するコンポーネントです。
     *
     * @returns {[]} 選択肢リスト
     * @constructor
     */
    const MonthOptions = () => {
        const options = [];
        for (let month = 1; month <= 12; month++) {
            options.push(<option key={month} value={month}>{month}</option>);
        }
        return options;
    };

    /**
     * スナックバーの閉じるイベント時に呼ばれます。
     *
     * @param event イベント
     * @param reason 理由
     */
    const handleCloseSnackbar = (event, reason) => {
        if ('clickaway' === reason) {
            return;
        }
        setSnackbarOpen(false);
    };

    /**
     * 時間の前後検証を実施します。
     *
     * @param start 開始時間
     * @param end 終了時間
     * @returns {boolean} true: 検証OK, false: 検証NG
     */
    const validationTime = (start, end) => {
        const now = new Date();
        const starts = start.split(':');
        const ends = end.split(':');
        const sdt = new Date(now.getFullYear(), now.getMonth(), now.getDate(), Number(starts[0]), Number(starts[1]));
        const edt = new Date(now.getFullYear(), now.getMonth(), now.getDate(), Number(ends[0]), Number(ends[1]));
        return sdt < edt;
    };

    return (
        <form className={classes.root}>
            <CssBaseline />

            <AppBar position='sticky'>
                <Toolbar>
                    <IconButton edge='start' className={classes.menuButton} color='inherit' aria-label='menu' onClick={() => setDrawerOpen(true)}>
                        <MenuIcon />
                    </IconButton>
                    <Typography variant='h6' className={classes.title}>
                        株式会社ベイクラフト
                    </Typography>
                    <Button color='inherit'>
                        <Avatar alt='avatar' src={user['profile']['image_48']} className={classes.userAvatar} />
                        {user['profile']['real_name']}
                    </Button>
                </Toolbar>
            </AppBar>

            <Drawer open={drawerOpen} onClose={() => setDrawerOpen(false)}>
                <div className={classes.drawerList} onClick={() => setDrawerOpen(false)} onKeyDown={() => setDrawerOpen(false)}>
                    <List component='nav'>
                        <ListItem button component={Link} to='/timerecord'>
                            <ListItemIcon><AccessTimeIcon/></ListItemIcon>
                            <ListItemText primary='勤怠記録'/>
                        </ListItem>
                        <Divider />
                        <ListItem button component={Link} to="/fixedtime">
                            <ListItemIcon><TimelapseIcon/></ListItemIcon>
                            <ListItemText primary="所定時間設定"/>
                        </ListItem>
                        <ListItem button component={Link} to='/breaktime'>
                            <ListItemIcon><TimerOffIcon/></ListItemIcon>
                            <ListItemText primary='休憩時間設定'/>
                        </ListItem>
                    </List>
                </div>
            </Drawer>

            <Dialog open={addModalOpen} onClose={() => setAddModelOpen(false)}>
                <DialogTitle>休憩時間追加</DialogTitle>
                <DialogContent>
                    <TextField label="年" fullWidth={true} required={true} margin="dense"
                               defaultValue={year}
                               onChange={(event) => setYear(event.target.value)} />
                    <FormControl required={true} margin="dense">
                        <InputLabel htmlFor="select-month">月</InputLabel>
                        <NativeSelect value={month} inputProps={{id: 'select-month'}}
                                      onChange={(event) => setMonth(event.target.value)}>
                            <MonthOptions />
                        </NativeSelect>
                    </FormControl>
                    <TextField autoFocus={true} label="客先" fullWidth={true} required={true} margin="dense" onChange={(event) => setCustomer(event.target.value)} />
                    <TextField label="開始時間" fullWidth={true} required={true} margin="dense" onChange={(event) => setStartTime(event.target.value)} />
                    <TextField label="終了時間" fullWidth={true} required={true} margin="dense" onChange={(event) => setEndTime(event.target.value)} />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClickDialogCancel} color="primary">
                        キャンセル
                    </Button>
                    <Button onClick={handleClickDialogAdd} color="primary">
                        追加
                    </Button>
                </DialogActions>
            </Dialog>

            <Snackbar open={snackbarOpen} autoHideDuration={6000} onClose={handleCloseSnackbar}>
                <Alert severity={severity} variant="filled" onClose={handleCloseSnackbar}>{message}</Alert>
            </Snackbar>

            <Container maxWidth='lg' className={classes.container}>
                <Toolbar className={classes.tableToolbar}>
                    <Typography variant="h6" className={classes.title}>
                        休憩時間設定
                    </Typography>
                    <Tooltip title="追加">
                        <IconButton onClick={handleClickAdd}>
                            <AddIcon />
                        </IconButton>
                    </Tooltip>
                    <Tooltip title="リロード">
                        <IconButton onClick={handleClickReload}>
                            <CachedIcon />
                        </IconButton>
                    </Tooltip>
                </Toolbar>
                <TableContainer>
                    <Table size="small" className={classes.table} aria-label="breaktime">
                        <TableHead>
                            <TableRow>
                                <TableCell align="center">年</TableCell>
                                <TableCell align="center">月</TableCell>
                                <TableCell align="center">客先</TableCell>
                                <TableCell align="center">開始時間</TableCell>
                                <TableCell align="center">終了時間</TableCell>
                                <TableCell align="center">操作</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows.map(row => (
                                <TableRow key={row.id}>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.year}
                                                       inputProps={{style: {textAlign: 'center'}}}
                                                       onChange={(event) => handleChangeYear(row, event.target.value)} />
                                        ) : (
                                            <span>{row.year}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <NativeSelect value={row.month}
                                                          onChange={(event) => handleChangeMonth(row, event.target.value)}>
                                                <MonthOptions />
                                            </NativeSelect>
                                        ) : (
                                            <span>{row.month}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.customer}
                                                       inputProps={{style: {textAlign: 'center'}}}
                                                       autoFocus={true}
                                                       onChange={(event) => handleChangeCustomer(row, event.target.value)} />
                                        ) : (
                                            <span>{row.customer}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.start_time}
                                                       inputProps={{style: {textAlign: 'center'}}}
                                                       onChange={(event) => handleChangeStartTime(row, event.target.value)} />
                                        ) : (
                                            <span>{row.start_time}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.end_time}
                                                       inputProps={{style: {textAlign: 'center'}}}
                                                       onChange={(event) => handleChangeEndTime(row, event.target.value)} />
                                        ) : (
                                            <span>{row.end_time}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <div>
                                                <IconButton aria-label="done" onClick={() => handleClickDone(row)}>
                                                    <DoneIcon />
                                                </IconButton>
                                                <IconButton aria-label="cancel" onClick={() => handleClickCancel(row)}>
                                                    <CancelIcon />
                                                </IconButton>
                                            </div>
                                        ) : (
                                            <div>
                                                <IconButton aria-label="edit" onClick={() => handleClickEdit(row)}>
                                                    <EditIcon />
                                                </IconButton>
                                                <IconButton aria-label="delete" onClick={() => handleClickDelete(row)}>
                                                    <DeleteIcon />
                                                </IconButton>
                                            </div>
                                        )}
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Container>
        </form>
    );
}
