import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Main from './routes/Main';
import PrivateRoute from './PrivateRoute';
import TimeRecord from './routes/TimeRecord';
import BreakTime from './routes/BreakTime';
import FixedTime from './routes/FixedTime';

export default function App() {
    return (
        <BrowserRouter>
            <Switch>
                <Route exact path="/"><Main/></Route>
                <PrivateRoute exact path="/timerecord">
                    <TimeRecord/>
                </PrivateRoute>
                <PrivateRoute exact path="/fixedtime">
                    <FixedTime/>
                </PrivateRoute>
                <PrivateRoute exact path="/breaktime">
                    <BreakTime/>
                </PrivateRoute>
                <Route path="*"><Main/></Route>
            </Switch>
        </BrowserRouter>
    );
}
